import React from 'react';
import './Forms.css'

 const  MsgError = () =>  (
  <div>
  <span>
invalid regisrertion
  </span>
  </div>);

class Register extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      name: '',
      isHide: true
      }
  }
 
  setIsHide = boolean => {
  this.setState({isHide: boolean})
 }
 
  onNameChange = (event) => {
    this.setState({name: event.target.value})
  }

   onEmailChange = (event) => {
    this.setState({email: event.target.value})
  }

  onPasswordChange = (event) => {
    this.setState({password: event.target.value})
  }

  saveAuthToken = (token) => {
    window.sessionStorage.setItem('token' , token)
  }
 

  onSubmitRegister = () => {
    fetch('https://pure-headland-69230.herokuapp.com/register', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        email: this.state.email,
        password: this.state.password,
        name: this.state.name
      })
    })
       .then(response => response.json())
      .then(user => {
        if (user.id) {
          this.props.loadUser(user)
          this.props.setIsLogedIn(true);
          this.saveAuthToken(user.id)
        } else (this.setIsHide(false))
      })
  }

  render() { 
    return (
      <article className="br3 ba b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center">
        <main className="pa4 black-80">
          <div className="measure">
            <form autocomplete="off" id="sign_up" className="ba b--transparent ph0 mh0">
              <h3 className="f1 fw6 ph0 mh0">Register</h3>
              <div className="mt3">
                <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
                <input
                aria-label= 'type name here'
                  className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100 hover-black"
                  type="text"
                  name="name"
                  id="name"
                  onChange={this.onNameChange}
                />
              </div>
              <div className="mt3">
                <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                <input
                aria-label= 'type email here'
                  className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100 hover-black"
                  type="email"
                  name="email-address"
                  id="email-address"
                  onChange={this.onEmailChange}
                />
              </div>
              <div className="mv3">
                <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                <input
                aria-label= 'type secret here'
                  className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100 hover-black"
                  type="password"
                  name="password"
                  id="password"
                  onChange={this.onPasswordChange}
                />
              </div>
            </form>
            <div className="">
              <input
                onClick={()=>{this.setIsHide(true); this.onSubmitRegister()}}
                className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib"
                type="submit"
                value="Register"
              />
            </div>
            {!this.state.isHide && <MsgError/>}
          </div>
        </main>
       </article>
    );
  }
}

export default Register;